  var code1=`#include<stdio.h>
#include<string.h>
#define  N  8
int n,path[N],st[N];
void dfs(int x){
    if(x==n){
        for(int i = 0 ; i < n;i++)printf("%d ",path[i]);
        puts("");
        return;
    }
    for(int i=1;i<=n;i++ ){//数字为从1到n
        if(!st[i]){
            st[i]=1;
            path[x]=i;
            dfs(x+1);
            st[i]=0;
        }
    }
}
int main(){
    scanf("%d",&n);
    dfs(0);
