<template>
    <el-card style="max-width: 1200px">
        <template #header><strong>快捷键</strong></template>
    ctrl+alt+ &nbsp;t&nbsp;&nbsp;：打开终端<br/>
    ctrl+shift+ &nbsp;+&nbsp;&nbsp;：终端放大<br/>
      </el-card>
      <el-divider></el-divider>
      <el-card style="max-width: 1200px">
        <template #header><strong>终端指令</strong></template>
        1、安装应用：<br/>
        在下载好deb文件之后，sudo dpkg -i hello.deb，之后应用可以在左下角菜单→全部中打开<br/>
        取消安装：sudo apt-get remove XXXXX<br/>

        2、Vim：<br/>
        ①首先sudo apt-get install vim。<br/>
        ②输入vim ~/Downloads/m.txt进入vim编辑界面,直接就可以输入了。<br/>
        ③编辑完成后，点击Esc退出编辑模式，然后输入「shift + ;」, 其实就是输入「:」，来进入「末行模式]<br/>
        ④输入wq再enter就是保存并退出，q！就是不保存并退出，w就是保存文件。<br/>
        Vim中撤销上一处修改：先进入「末行模式]，然后u。<br/>

        3、解压：把JDK文件解压到/usr/lib/jvm目录下：<br/>
    <highlightjs  language="c++" :autodetect="false" :code="code"></highlightjs>
        4、gedit的使用：<br/>
        进入到文件所在的文件夹，然后gedit XXX，回车后就可以编辑了。<br/>

      </el-card>
    
    </template>
    <script setup>
    var code=`cd Downloads
sudo tar -zxvf ./jdk-8u162-linux-x64.tar.gz -C /usr/lib/jvm
 
##解压各种压缩包用的指令：
tar -xvf filename.tar
tar -zxvf filename.tar.gz
tar -zxvf filename.tgz
tar -jxvf filename.tar.bz2
tar -xZvf filename.tar.Z
unrar e filename.rar # 解压到当前目录
unrar x filename.rar /path/to/extract 
unzip filename.zip -d filepath`
    </script>
