        <template #header><strong>梯度下降算法</strong></template>
        深度学习最重要的就是梯度下降算法。例如预测房价，假设m个点 &lt; xi,yi&gt;代表房屋面积和房价。现在假设xy坐标系为一条直线
        ，设y=wx+b，损失J=
        <vue-latex :expression="'\\frac{1}{2m} \\sum (\\bigtriangleup y)^{ 2} '" display-mode :font-size="30"/>，带入这里就是
        J=<vue-latex :expression="'\\frac{1}{2m} \\sum [yi-(w\\bullet xi+b)]^{ 2}'" display-mode :font-size="30"/> 求
        <vue-latex :expression="'\\frac{\\partial J}{\\partial w} \\rightleftharpoons w_{n+1} =w_{n}-\\eta \\frac{\\partial J}{\\partial w}'" display-mode :font-size="30"/> 
        即从w1，我可以找到比这个w1更好的w2，w2还是不够，那就继续计算，最终找到最优化的w，直到<vue-latex :expression="'\\frac{\\partial J}{\\partial w}'"  :font-size="20"/>=0或者直到<vue-latex :expression="'\\w_{n+1}\\approx  w_{n}'"  :font-size="20"/>
        这个时候就不需要迭代了，我们找到了最优解。事实上刚才只考虑到了w，还有一个b，真正运用是w和b同时进行优化的。房价有可能取决于
        很多种因素，把这些因素记为<vue-latex :expression="'x_{1}, x_{2},x_{3}... x_{n}'"  :font-size="20"/>，这样一来你可能就会发现房价是这样一个函数：
        <vue-latex :expression="'y=w_{1}x_{1}+w_{2}x_{2}+w_{3}x_{3}... w_{n}x_{n}+b '" display-mode :font-size="30"/>被称为回归的分析。除此之外还有分类的
        分析。也就是比如给你一堆肿瘤的照片，你来区分哪些是良性哪些是恶性。
    </el-card>
