<template>


    <el-card style="max-width: 1200px">
        <template #header><strong>判断质数</strong></template>
        思想：判断n为何数，对于非质数12,3和4都是他的因子，因子都是成对出现的，因此我们只需要估计较小的那个，即从2到sqrt(n)，判断每个数是否是他的因子即可。以下代码为y总优化版本。
        <highlightjs  language="c++" :autodetect="false" :code="code1"></highlightjs>
      </el-card>
      <el-card style="max-width: 1200px">
        <template #header><strong>判断质数（法二）</strong></template>
        思想：利用了一个原理：除2、3外的任何质数，除以6的余数一定是1或5。
        <highlightjs  language="c++" :autodetect="false" :code="code2"></highlightjs>
      </el-card>
      
    </template>
    <script setup>
    var code1=`bool is_prime(int n){
    if(n < 2) return false;
    for(int i = 2;i <= n / i;i ++){ //优化内容
        if(n % i == 0){
            return false;
        }
    }
    return true;
}`
    var code2=`#include<iostream>
#include<cmath>
using namespace std;
const int N=103;
int is_prime(int a)
{
	if(a==1)return 0;
	if(a==2||a==3)return 1;
	if(a%6!=1&&a%6!=5)return 0;
	for(int i=5;i<=sqrt(a);i+=6)
      	if(a%i==0||a%(i+2)==0)return 0;
	return 1;
}`
    </script>
