<template>
        <el-card style="max-width: 1200px">
            <template #header><strong>阅前须知（拉链法）:</strong></template>
            <el-text>
                1、多个数映射到较少数，多数都是直接mod映射。有开放寻址法和拉链法。<br/>
                2、拉链法：先开一个数组来存储哈希值，例如映射方法是x mod 10^5，则开一个10^5长度的数组<br/>
                        这个数组每一个位置看做一个槽，每个槽上都拉一条链，用来存储这个槽上当前已经有的所有的数。<br/>
                        例如：第一次11映射到3的位置，23也映射到3，则3下面有11→23。<br/>
                3、一般哈希表只有添加和查找，不会有删除。<br/>
                <strong>查找：</strong>先看x在哪个槽上，再看槽对应的单链表里面存不存在x就行<br/>
                <strong>添加：</strong>比如要添加x，先求一下h(x)，看一下x对应哪个槽，然后把x插到槽对应的链上（单链表<br/>
                <strong>删除（如果非要）：</strong>在槽对应的链上的x处打上bool标记删除。不是真的删除。<br/>
                注意：一般模的数要取质数<br/>

                首先预习数组模拟单链表的插入操作：e[idx]=x;ne[idx]=head;head=idx++;<br/>
                先给点赋值，再让他指向head原本指向的点，head现在指向第一个元素了之后，再让idx++，为下一个元素插入做准备<br/>
                （初始化：head=-1，idx=0）<br/>
            </el-text>
            <highlightjs  language="c++" :autodetect="false" :code="link_code"></highlightjs>
      </el-card>
      <el-card style="max-width: 1200px">
            <template #header><strong>阅前须知（开放寻址法）:</strong></template>
            <el-text>
                开放寻址法只用到了一个一维数组，不用链表。只是这个一维数组要开到N的2-3倍。<br/>
                原理就是h(x)=k的话，先看第k个位置是否被占，如果有则继续找下一个，直到找到空的坑位。<br/>
                <strong>添加：</strong>如上所示<br/>
                <strong>查找：</strong>从第k个前往后找，如果有且是x，则找到，如果有则下一个坑位，如果空则不存在<br/>
                <strong>删除：</strong>从前往后找x，如果找到x，一般不会真的删掉，而是打一个标记x是否存在
            </el-text>
            <highlightjs  language="c++" :autodetect="false" :code="open_code"></highlightjs>

      </el-card>
      
    </template>
    <script setup>
    var link_code =`#include <iostream>
#include<cstring>
using namespace std;
const int N=1e5+3;
//槽  链表的值  下一个值  当前用到了哪个位置
int h[N],e[N],ne[N],idx;
void insert(int x){
   //x范围：-10^9~10^9

   //先想一个哈希函数把他映射到0~10^5的任意一个数
   int k = (x%N+N)%N;//k为哈希值  注意，如果是负数的话，他的取模是负数
   
   //插入实际就是当前的点插入h[k]对应的链表上(参考链表头插法)  h[k]上存的是这个链表里面第一个节点的下标 e[i]就是当前这个点   ne[i]就是下一个点
   e[idx]=x,ne[idx]=h[k],h[k]=idx++;
   cout<<"h["<<k<<"]="<<h[k];
}
bool find(int x){
   int k=(x%N+N)%N;//先找到位置
   //再遍历链表
   for(int i = h[k] ; i != -1 ; i = ne[i])
   if(e[i]==x)return true;
   return false;
}
int main(){
   int n;
   scanf("%d",&n);
   memset(h,-1,sizeof(h));
   while(n--)
   {
      char op[2];
      int x;
      scanf("%s%d",op,&x);//一般要用scanf读字符串，因为他可以忽略掉空格、回车、制表符
      //一般不要用scanf读字符（有些数据坑，里面会加额外空格）
      if(op[0]=='I')insert(x);
      else {
         if(find(x))puts("Yes");
         else puts("No");
      }
   }
}
`
var open_code=`#include <iostream>
#include<cstring>
using namespace std;
const int N=2e5+3,null=0x3f3f3f3f;
int h[N];
void insert(int x){
  //...
}
int find(int x){//如果x在hash表中存在，返回的是位置;若不存在，返回的是它应该存储的位置
     int k = (x%N+N)%N;
   
   while(h[k] != null && h[k]!= x)
   {
    k++;
    if(k==N)k=0;//如果看到了最后一个坑位，那么就要循环重新从第一个坑位开始看
   }
   return k;
}

int main(){
   int n;
   scanf("%d",&n);
   memset(h,0x3f,sizeof h);
   while(n--)
   {
      char op[2];
      int x;
      scanf("%s%d",op,&x);
        int k = find(x);
      if(op[0]=='I')h[k]=x;
      else {
         if(h[k]!=null)puts("Yes");
         else puts("No");
      }
   }
}
`
