ROS中编写程序步骤：<br/>
1、先创建一个工作空间<br/>
2、再创建一个功能包<br/>
3、编辑源文件<br/>
4、编辑配置文件<br/>
5、编译并执行<br/><br/>
不同语言（C++、python）只是在3、4步骤上细节存在差异，其他流程基本一致<br/>

一个catkin软件包有什么组成：必须有一个package.xml文件，提供有关该软件包的元信息<br/>
必须有一个CMakeLists.txt文件。每个包必须有自己的目录，即同一个目录下不能有嵌套的或者多个软件包存在<br/>
最简单的软件包就像：my_package/<br/>
CmakeLists.txt<br/>
package.xml<br/>
CMakeLists.txt在~/catkin_ws/src下，package.xml 在/catkin_ws/src/beginner_tutorials下<br/><br/>


catkin路径：/Downloads/livox_ros_driver-master/ws_livox/catkin<br/><br/>


构建软件包：<br/>
首先建文件夹  mkdir -p ~/catkin_ws/src<br/>
进入文件夹cd ~/catkin_ws/<br/>
再catkin_make<br/>
source devel/setup.bash<br/>
cd ~/catkin_ws/src<br/>
使用catkin_create_pkg命令创建一个名为beginner_tutorials的新软件包，这个软件包依赖于std_msgs、roscpp和rospy：<br/>
catkin_create_pkg beginner_tutorials std_msgs rospy roscpp<br/>
到此，前两部完成。接下来是3、4部。<br/>
