
<template>


    <el-card style="max-width: 1200px">
        <template #header><strong>输出所有序列</strong></template>
        acwing842.排序数字。输入一个数n，输出从1到n所有序列<br/>
        例如：输入3，则输出123,132,213,231,312,321<br/>
        为什么main中dfs(0)?例如输入的是3，从第0个位置开始，再看第一个位置，再看第二个位置，再看第三个位置（就是空了，此时输出）<br/>
        之后的for循环逐个填入，st存的是1,2,3三个数字哪几个已被使用，使用了则为true。path存的是已经走过的路径。
        <highlightjs  language="c++" :autodetect="false" :code="code_normal"></highlightjs>
      </el-card>
      
    </template>
    <script setup>
        var code_normal=`#include<stdio.h>
#include<string.h>
#define  N  8
int n,path[N],st[N];
void dfs(int x){
    if(x==n){
        for(int i = 0 ; i < n;i++)printf("%d ",path[i]);
        puts("");
        return;
    }
    for(int i=1;i<=n;i++ ){//数字为从1到n
        if(!st[i]){
            st[i]=1;
            path[x]=i;
            dfs(x+1);
            st[i]=0;
        }
    }
}
int main(){
    scanf("%d",&n);
    dfs(0);
}
    `
    </script>
