    var code2=`#include "ros/ros.h"
#include "std_msgs/String.h"
#include<sstream>
/*
    发布方实现：
        1、包含头文件
        2、初始化ROS节点
        3、创建节点句柄
        4、创建发布者对象
        5、编写发布逻辑并发布数据
*/
int main(int argc,char *argv[]){
    setlocale(LC_ALL,"");
    //3、初始化ros节点
    ros::init(argc,argv,"erGouZi");
    //3、创建节点句柄
    ros::NodeHandle nh;
    //4、创建发布者对象
    ros::Publisher pub = nh.advertise<std_msgs::String>("fangzi",10);//可以通过rostopic echo fangzi 来查看发布的话题。泛型：发布的消息类型，参数1：要发布的话题，参数2：队列中最大保存的消息数，超出时时间早的先销毁
    //5、编写发布逻辑并发布数据
    //要求以10HZ的频率发布数据，并且文本后添加编号
    //先创建被发布的消息
    std_msgs::String msg;
    //发布频率
    ros::Rate rate(10);//这里传的是频率
    //设置频率
    int count = 0;
    ros:Duration(3).sleep();//为了防止第一条数据接收不到
    //编写循环，循环中发布数据
    while(ros::ok()){//ok就是节点还存在
        count ++;
        std::stringstream ss;
        ss <<"hello --->"<<count;
        // msg.data="hello";
        msg.data=ss.str();
        pub.publish(msg);
        //添加日志
        ROS_INFO("发布的数据是：%s",ss.str().c_str());
        rate.sleep();
    }
    return 0;
}`
