    <el-card style="max-width: 1200px">
        <template #header><strong>noSQL思想和HBase架构</strong></template>
        <br/>
非关系型和关系型数据库的区别就是关系型数据库以表格的形式存储数据，而非关系型以（k，v）的形式存储数据<br/>
      命名空间（NameSpace）：<br/>类似于关系型数据库中的Database概念，每个命名空间下有多个表。HBase自带两个命名空间hbase和default，
      hbase存放的是HBase内置的表，default是用户默认使用的命名空间。<br/><br/>

      行键（RowKey）：<br/>HBase 表中的每行数据都由一个 RowKey 和多个 Column（列）组成，几行数据可以作为一个region。数据是按照 RowKey 的字典顺序存储的，
      并且查询数据时只能根据 RowKey 进行检索，所以 RowKey 的设计十分重要。<br/><br/>
      <img src="../../../assets/pics/hbase/struct.png">
      列族：<br/>HBase 中的每个列都由 Column Family（列族）和 Column Qualifier（列限定符）进行限
定，例如 info：name，info：age。建表时，只需指明列族，而列限定符无需预先定义。<br/><br/>

时间戳（TimeStamp）：<br/>用于标识数据的不同版本（version），每条数据写入时，如果不指定时间戳，系统会
自动为其加上该字段，其值为写入 HBase 的时间。<br/><br/>

单元（cell）：<br/>由 {rowkey, column Family：column Qualifier, time Stamp} 唯一确定的单元。cell 中的数
据是没有类型的，全部是字节码形式存贮。<br/><br/>
      列族存储（store）。列族就是把通常需要一并访问的相关数据分成组。在nosql数据库中，“键”用来index某一数据值。
      键一般需要包括行键、列族名、列键、时间戳。行键和列键用来索引，时间戳运行值保存多个版本。列组存储有点像数据表被压缩成一维的键和值。
      关系型数据库存储稀疏的数据不太在行，而列组存储就是为这个设计的。<br/><br/>
      
      HBase的基本架构：<br/><br/>
      1、RegionServer<br/>为 Region 的管理者，其实现类为 HRegionServer，主要作用如下:<br/>

① 对于数据的操作：get, put, delete；<br/>
② 对于 Region 的操作：splitRegion、compactRegion。<br/>

2、Master <br/>是所有 RegionServer 的管理者，其实现类为 HMaster，主要作用如下：<br/>

① 对于表的操作：create, delete, alter<br/>
② 对于 RegionServer的操作：分配 regions 到每个 RegionServer，监控每个 RegionServer 的状态，负载均衡和故障转移。<br/>

3、Zookeeper：<br/>HBase 通过 Zookeeper 来做 Master 的高可用、RegionServer 的监控、元数据的入口以及集群配置的维护等工作。<br/>

4、HDFS <br/>为 HBase 提供最终的底层数据存储服务，同时为 HBase 提供高可用的支持。<br/>

为什么叫分布式存储？就是一个老大带着一帮小弟。HBase通过ZooKeeper来实现分布式存储。老大就是master，下面是很多个Region Server
      </el-card>



