
    <template>


<el-card style="max-width: 1200px">
    <template #header><strong>三数之和</strong></template>
    力扣第15题，一个数组，输出所有三数之和为0的组合，且i！=j，j！=k，i！=k。<br/>
    首先进行排序，记住，<div style="color: coral;">双指针的前提：已经排好序</div><br/>
    采用双指针做法。首先固定i（i=1,2...渐渐往后），然后j从i的下一个往后，k从最后一个往左，找到和为0的，直到j=k为止。<br/>
    期间如果出现三数之和大于0，则k往前一位，如果出现三数之和小于0，则j往后一位。
    <highlightjs  language="c++" :autodetect="false" :code="code_normal"></highlightjs>
  </el-card>
  
</template>
<script setup>
    var code_normal=`class Solution {
public:
    vector<vector<int>> threeSum(vector<int>& nums) {
        vector<vector<int>> ans;
        int n = nums.size();
        sort(nums.begin(), nums.end());
        for (int i = 0; i < n; ++i) {
            if (i > 0 && nums[i] == nums[i-1]) continue;
            int l = i+1, r = n-1;
            while (l < r) {
                if (nums[i]+nums[l]+nums[r] < 0) {
                    ++l;
                } else if (nums[i]+nums[l]+nums[r] > 0) {
                    --r;
                } else {
                    ans.push_back({nums[i], nums[l], nums[r]});
                    while (l+1 < nums.size() && nums[l] == nums[++l]);
                    while (r-1 > 0 && nums[r] == nums[--r]);
                }
            }
        }
        return ans;
    }
};
`
</script>
