<el-card style="max-width: 1200px">
    <template #header><strong>z字形变换</strong></template>
    力扣第六题，输入一个字符串和z的行数，将这个字符串以w字形排列，按最后排出的来输出
    <highlightjs  language="c++" :autodetect="false" :code="code_normal"></highlightjs>
  </el-card>
  
</template>
<script setup>
    var code_normal=`#include<cstring>
    #include<iostream>
    #include<vector>
    class Solution {
    public:
        string convert(string s, int numRows) {
            int len=s.size(),i=0,flag=1,j=1,tops[1000]={-1};
            if(numRows<2) return s;
            string ass[1000]={""},res="";
            while(i<len){
                    if(j==1)flag=1;
                    ass[j].push_back(s[i]);
                    j+=flag;
                    if(j==numRows)flag=-1;
                    i+=1;
            }
            for(i=1;i<=numRows;i++) res.append(ass[i]);
            return res;
            }
    };
`
</script>
