#include "ros/ros.h"
#include "pcl/io/pcd_io.h"
#include "pcl_conversions/pcl_conversions.h"
#include "sensor_msgs/PointCloud2.h"

void doBag(const sensor_msgs::PointCloud2 &point){
    pcl::PointCloud<pcl::PointXYZL> pcd;
    pcl::fromROSMsg(point,pcd);
    pcl::io::savePCDFileASCII("0.pcd",pcd);
    //   // 打印点云信息
    // std::cout << "Received point cloud with " << pcd.points.size() << " points." << std::endl;
    
