    //     std::cout << "Point " << i << ": (" << pcd.points[i].x << ", " << pcd.points[i].y << ", " << pcd.points[i].z << ")" << std::endl;
    // }
}

int main(int argc, char *argv[])
{
    setlocale(LC_ALL,"");
    ros::init(argc,argv,"volix_subscriber");

    pcl::PointCloud<pcl::PointXYZL>::Ptr cloud(new pcl::PointCloud<pcl::PointXYZL>);
    ros::NodeHandle nh;
    ros::Subscriber sub = nh.subscribe("/livox/lidar",10,doBag);
    ros::spin();
    return 0;
}`
