<template>


    <el-card style="max-width: 1200px">
        <template #header><strong>最长回文（基本方法）</strong></template>
        思想：有两种回文，一种是"bb"型，一种是"aba"型，所以要两种。从中间开始扩散的思想。
        <highlightjs  language="c++" :autodetect="false" :code="code_normal"></highlightjs>
      </el-card>
      <el-divider></el-divider>
      <el-card style="max-width: 1200px;">
        <template #header><strong>最长回文（dp方法）</strong></template>
        -<strong>思想：</strong>对于任何回文，去掉头尾依然是回文，所以它具有天然的状态转移性。如果一个子串的两头相等，我们就要看他们中间的是不是回文就行。<br/>
        -<strong> 状态：</strong>dp[i][j]表示子串s[i...j]是否是回文子串<br/>
        -因此得到<strong>状态转移方程：</strong>dp[i][j]<el-text type="danger" tag="mark" size="Large">=</el-text>(s[i]==s[j])and dp[i+1][j-1]<br/>
        &nbsp;边界条件：{{shi}}，整理得 j - i < 3（也就是只有当中间长度小于等于2时）<br/>
        -<strong> 初始化：</strong>dp[i][i] = true<br/>
        -<strong> 输出：</strong>在得到一个状态的值为true的时候，记录起始位置和长度，填表完成以后再截取<br/>
        -<strong>例如：</strong>字符babab，下标为01234，由于dp[i][j]参考左下方的值，故先升序填列，再升序填行。<br/>
        <el-table :data="tableData" style="margin-left: 350px;">
      <el-table-column type="index" />
      <el-table-column prop="flag0" label="0" width="70" />
      <el-table-column prop="flag1" label="1" width="70" />
      <el-table-column prop="flag2" label="2" width="70"/>
      <el-table-column prop="flag3" label="3" width="70"/>
      <el-table-column prop="flag4" label="4" width="70"/>
    </el-table><br/>
    这个表的意思是先把表的主对角线填入TRUE，因为单个都为回文。左边的01234表示i，右边的01234表示j，（0,1）表示从0到1的子串（长度为2）是否为回文。
        <highlightjs  language="c++" :autodetect="false" :code="code_dp"></highlightjs>
      </el-card>
    </template>
    <script setup>
        var code_normal=`class Solution {
public:
    string longestPalindrome(string s) {
        string res;
        for (int i = 0; i < s.size(); i ++ ) {
            int l = i - 1, r = i + 1;
            while (l >= 0 && r < s.size() && s[l] == s[r]) l --, r ++ ;
            if (res.size() < r - l - 1) res = s.substr(l + 1, r - l - 1);

            l = i, r = i + 1;
            while (l >= 0 && r < s.size() && s[l] == s[r]) l --, r ++ ;
            if (res.size() < r - l - 1) res = s.substr(l + 1, r - l - 1);
        }

        return res;
    }
};
`
var code_dp=`class Solution {
    public String longestPalindrome(String s) {
        if(s == null || s.length() == 0) {
            return "";
        }

        int n = s.length();
        boolean[][] isPalindrome = new boolean[n][n];

        for(int i = 0; i < n; i++) {
            isPalindrome[i][i] = true;
        }

        for(int j = 0; j < n; j++) {
            for(int i = j - 1; i >= 0; i--) {
                if(j - i == 1) {
                    isPalindrome[i][j] = (s.charAt(i) == s.charAt(j));
                } else {
                    isPalindrome[i][j] = isPalindrome[i + 1][j - 1] && (s.charAt(i) == s.charAt(j));
                }
            }
        }

        String res = "";
        for(int i = 0; i < n; i++) {
            for(int j = i; j < n; j++) {
                if(isPalindrome[i][j] && j - i + 1 > res.length()) {
                    res = s.substring(i, j + 1);
                }
            }
        }

        return res;
    }
}
`
var shi="j - 1 - ( i + 1 ) + 1 < 2"
