<el-card style="max-width: 1200px">
    <template #header><strong>完美序列</strong></template>
    acwing1571 完美序列   <br/>
    输入p和数组  从输入的数组中选出最多的数列，（可以打乱，注意是选出）输出长度。数列满足的条件是M<=m*p<br/>
· i指向最大的数<br/>
· j指向最小的数<br/>
· 如果不符合M<=m*p则向右移动j指针，直至符合条件为止<br/>
    <highlightjs  language="c++" :autodetect="false" :code="per_code"></highlightjs>
  </el-card>
