  <el-card style="max-width: 1200px">
    <template #header><strong>向量内积</strong></template>
    acwing3288 向量内积   <br/>
    输入两个稀疏矩阵，矩阵中的每个非零元素通过二元组index，value表示<br/>
输出两个向量的内积<br/>
    <highlightjs  language="c++" :autodetect="false" :code="per_code"></highlightjs>
  </el-card>

  </template>
<script setup>
var per_code=`#include<iostream>
#include<algorithm>
using namespace std;
const int N = 1e5+10;
int n,p,len;
int a[N];
int main(){
    cin>>n>>p;
    for(int i = 0 ; i < n ; i ++)cin>>a[i];
    sort(a,a+n);//注意sort用法  默认从小到大  从大到小：sort(num,num+10,greater<int>());
    for(int i=0,j=0;i<n;i++){
        while(a[j]*p>a[i]&&j<n) j++;
        len=max(len,i-j+1);
    }
    printf("%d",len);
    return 0;
}`
var mod_code=`for(int i=0;i<n;i++)
    {
        int j=i+1;
        while(j<n&&check(j)
        {
            //具体操作
        }
    }`
var xiangliang_code =`#include<bits/stdc++.h>
using namespace std;
typedef long long ll;
typedef pair<int,int> PII;

int n,a,b;
vector<PII> u,v;

int main(){

    //添加数据
    cin>>n>>a>>b;
    while(a--){
        int k,val;
        cin>>k>>val;
        u.push_back(make_pair(k,val));
    }
    while(b--){
        int k,val;
        cin>>k>>val;
        v.push_back(make_pair(k,val));
    }

    //双指针算法
    ll res = 0;
    int i=0,j=0;
    while(i<u.size() && j<v.size()){
        if(u[i].first == v[j].first){
            res+=(ll)u[i].second*v[j].second;
            i++,j++;
        }
        else if(u[i].first < v[j].first) i++;
        else j++;
    }
    cout<<res<<endl;

    return 0;
}

`
</script>
