    <el-card style="max-width: 1200px">
        <template #header><strong>ROS介绍</strong></template>
    目标：提高机器人研发中的软件复用率<br/><br/>
    特点：<br/>1、点对点的设计：一个功能就是一个节点。用来提高软件的复用<br/>
    2、多语言支持：每一个节点都是一个功能，分别可以使用不同的编程语言进行编程，如C++、Python、Java等编程语言<br/>
    3、架构精简，集成度高<br/>
    4、组件化工具包丰富：有恒多仿真界面工具如rviz，仿真环境gazebo，rqt_plot图形化显示工具<br/>
    5、免费并且开源<br/><br/>
    四位一体：ROS=通讯机制+开发工具+应用功能+生态系统<br/><br/>
通讯机制：点对点通讯。通讯方式为两种：话题和服务通讯。话题：发布订阅模式，异步无反馈，服务：请求相应模式，同步有反馈<br/>
开发工具：rviz等<br/>
应用功能：功能与节点不同。功能可以理解为是一个大功能，节点是小功能<br/>
生态系统：ROS不是一个单独的软件，他所在的环境有各个大牛人、公司。<br/><br/><br/><br/>

节点（Nodes）：节点是一个可执行文件，它可以通过ROS来与其他节点进行通信。<br/>
消息（Messages）：订阅或发布话题时所使用的ROS数据类型。<br/>
话题（Topics）：节点可以将消息发布到话题，或通过订阅话题来接收消息。<br/>
主节点（Master）：ROS的命名服务，例如帮助节点发现彼此。<br/>
rosout：在ROS中相当于stdout/stderr（标准输出/标准错误）。<br/>
roscore：主节点 + rosout + 参数服务器（会在以后介绍）。<br/><br/>


ROS节点使用ROS客户端库与其他节点通信<br/><br/>

ROS客户端库可以让用不同编程语言编写的节点进行相互通信：<br/>
rospy = Python客户端库<br/>
roscpp = C++客户端库<br/><br/><br/>

理解ROS服务和参数<br/><br/><br/>



<img src="../../../assets/pics/ros/struct.jpg"><br/>
workspace:自定义的工作空间<br/>
src：源码<br/>
package ：功能包（ ROS 基本里元）包含多个节点、库与配置文件，包名所有字母小与，只能由字母、数字<br/>
cMakelists . txt 配置编译规则，比如源文件、依赖项、目标文件<br/>
package . xml 包信息，比如：包名、版本、作者、依赖项．..（以前版本是 manifest . xml )<br/>
|-- scripts 存储 python 文件<br/>
 l -- src 存储 C +＋源文件<br/>
|-- include 头文件<br/>
 l -- msg 消息通信格式文件<br/>
|-- srv 服务通格式文件 <br/>l -- action 动作格式文件<br/>-- launch 可一次性运行多个节点<br/>
-- config 配置信息<br/>
|-- CMakeLists . txt ：编译的基本配置<br/><br/><br/>

ROS角色：<br/>
1、master-----管理者  管理匹配话题   可以根据话题建立发布者和订阅者之间的连接<br/>
2、talker-----发布者<br/>
3、listener---订阅者<br/><br/>

a、talker广播自己有什么<br/>
b、listener发布自己想要什么<br/>
c、master按对应的将talker推给listener<br/>
d、listener向talker发起连接请求<br/>
e、talker响应同意请求<br/>
f、listener成功与talker连接<br/>
g、talker向listener发布消息<br/><br/>

注意：<br/>
1、使用的协议有RPC和TCP<br/>
2、步骤0和步骤1没有顺序关系<br/>
3、talker和listener都可以存在多个<br/>
4、talker和listener建立连接后，master就可以关闭了<br/>
5、上述实现流程已经封装了，以后直接调用即可<br/>

