    <script setup>
import { reactive } from 'vue';
var item=reactive({guonihe:["1、过度分支：决策树可能在训练集上生成过多的分支，每个分支都对应于数据的特定子集，导致树形结构非常深，并且过于复杂。这可能会导致模型过拟合训练数据，无法泛化到新数据。"
,"2、忽略噪声：决策树可能会在训练集中过度关注噪声或异常值，试图适应这些数据，而忽略了真实数据的潜在模式。这会导致模型在未来遇到新数据时表现不佳。",
"3、特征过度拟合：如果训练数据中的特征数量过多，决策树可能会试图使用所有特征进行拟合，即使一些特征与目标变量没有实际关联。这可能导致模型在真实数据上表现不佳。",
"4、训练数据不足：当训练数据量不足时，决策树可能会过拟合现有数据，因为它无法捕获数据的真实分布。这尤其在数据维度高或特征数量多的情况下容易发生。",
"5、未进行合适的剪枝：如果没有进行适当的剪枝操作，决策树可能会在训练集上过拟合，剪枝是一种减少树的复杂度、防止过拟合的技术。"
],
niheExamples:["1、医学诊断：假设用于医学诊断的决策树模型在训练时对训练集上的医疗数据进行了过度拟合。例如，模型可能会过于依赖某些不太常见的症状，而忽略了更为常见但不是那么显著的症状，导致在实际临床应用中性能不佳。",
,"2、金融风险评估：决策树在金融领域常用于风险评估。然而，如果模型在训练过程中过度依赖于一些不太常见的风险指标或者特定的市场情况，而忽略了更普遍的经济指标或者风险因素，那么它可能无法准确地评估真实的风险水平。"
,"3、客户细分：假设一个零售企业使用决策树来对客户进行细分，以制定个性化的营销策略。如果决策树在训练过程中过度拟合了少数高价值客户的行为模式，而忽略了大多数普通客户的特征，那么最终的细分结果可能会偏向于高价值客户，而无法满足整体市场的需求。"
],
randomForest:["1、随机抽样：从训练数据集中随机选择一定数量的样本，允许重复选择（有放回抽样），用于训练每棵决策树。",
"2、随机特征选择：对于每个决策树的每个节点，随机选择一定数量的特征进行考虑，而不是使用全部特征。这有助于提高模型的多样性，减少过拟合的风险。",
"3、构建决策树：针对每个随机抽样的数据子集和随机选择的特征子集，构建一棵决策树。在构建过程中，使用信息增益、基尼系数等指标选择最佳的属性进行节点分裂，直到达到树的最大深度或者节点中的样本属于同一类别。",
"4、集成预测：对于分类任务，通过对每棵决策树的预测结果进行投票或取平均值来确定最终的分类结果。对于回归任务，可以取所有决策树的预测值的平均值作为最终的预测结果。"]
,code:`# 导入必要的库
from sklearn.ensemble import RandomForestClassifier  # 导入随机森林分类器
from sklearn.datasets import load_iris  # 导入鸢尾花数据集
from sklearn.model_selection import train_test_split  # 导入数据集划分函数
from sklearn.metrics import accuracy_score  # 导入准确率评估指标

# 加载鸢尾花数据集
iris = load_iris()

# 将数据集划分为训练集和测试集
X_train, X_test, y_train, y_test = train_test_split(iris.data, iris.target, test_size=0.2, random_state=42)

# 初始化随机森林分类器，并设置参数
rf_classifier = RandomForestClassifier(n_estimators=100, random_state=42)

# 在训练集上拟合随机森林模型
rf_classifier.fit(X_train, y_train)

# 在测试集上进行预测
predictions = rf_classifier.predict(X_test)

# 计算模型的准确率
accuracy = accuracy_score(y_test, predictions)
print("Accuracy:", accuracy)
`}
)
    </script>
