<script setup>
var code=`//快排 
#include <iostream>
using namespace std;
//quick_sort algorithm
const int N = 1E6+10;
int n = 0;
int q[N];

void quick_sort(int q[],int l,int r){
    if(l>=r)return;
    int x = q[(l+r)/2];
    int i = l-1 , j = r+1;
    while(i<j){
        do i++;while(q[i]<x);
        do j--;while(q[j]>x);
        if(i<j){
            int temp = q[j];
            q[j] = q[i];
            q[i] = temp;
        }
    }
    quick_sort(q,l,j);
    quick_sort(q,j+1,r);
}
int main() {
    scanf("%d",&n);//多少个数
    for(int i = 0 ;i<n;i++){
        scanf("%d",&q[i]);
    }
    quick_sort(q,0,n-1);
    for(int i=0;i<n;i++){
        printf("%d ",q[i]);
    }
    return 0;
}`
</script>
