      <template #header><strong>实操1：话题发布</strong></template>
      需求：实现乌龟运动控制，让乌龟作圆周运动<br/>
      通过rosrun turtlesim turtlesim_node<br/>
      rosrun turtlesim turtle_teleop_key来启动小乌龟<br/>
      通过rostopic info /turtle1/cmd_vel看到话题下的消息类型、发布方、接收方<br/>
      通过rosmsg show geometry_msgs/Twist来显示消息的格式。其中linear是线速度，angular是角速度。对于乌龟来说，只有x方向的线速度，只有z方向的角速度，也就是偏航。偏航z：左右，俯仰y：上下，翻滚x：旋转<br/>
      通过rostopic echo /turtle1/cmd_vel来显示乌龟速度<br/><br/>

      
      1、极简实现：rostopic pub -r 10 /turtle1/cmd_vel geometry_msgs/Twist<br/>
      2、C++实现：<highlightjs  language="c++" :autodetect="false" :code="code1"></highlightjs>
      3、Python实现：<highlightjs  language="python" :autodetect="false" :code="code2"></highlightjs>
    </el-card>
    
