void cb(const ros::TimerEvent& event){
    ROS_INFO("-----------------");
    ROS_INFO("函数被调用的时刻：%.2f",event.current_real.toSec());
}

int main(int argc,char *argv[]){
    setlocale(LC_ALL,"");
    ros::init(argc,argv,"hello_time");
    ros::NodeHandle nh;
    //1、获取当前时间
    ros::Time right_now=ros::Time::now();
    ROS_INFO("当前时刻：%.2f",right_now.toSec());//获取距离 1970年01月01日 00:00:00 的秒数
    ROS_INFO("当前时刻：%d",right_now.sec);//获取距离 1970年01月01日 00:00:00 的秒数

    //2、设置当前时刻
    ros::Time t1(100.35);
    ROS_INFO("t1 = %.2f",t1.toSec());

    //持续时间-----------------------------------
    //1、程序执行中停顿五秒
    ros::Time start=ros::Time::now();
    ROS_INFO("开始休眠：%.2f",start.toSec());

    ros::Duration du(4.5);
    du.sleep();

    ros::Time end=ros::Time::now();
    ROS_INFO("休眠结束：%.2f",end.toSec());

    //时间运算------------------------------------
    ros::Time begin=ros::Time::now();
    ros::Duration du1(5);
    ros::Time stop=begin+du1;
    ROS_INFO("当前时刻：%.2f",begin.toSec());
    ROS_INFO("当前时刻：%.2f",stop.toSec());
    ros::Duration du2=begin-stop;
    ROS_INFO("时刻相减：%.2f",du2.toSec());
    ros::Duration du3=du1-du2;
    ros::Duration du4=du1+du2;
    ROS_INFO("du1-du2：%.2f",du3.toSec());
    ROS_INFO("du1+du2：%.2f",du4.toSec());

    //设置运行频率--------------------------------
    // ros::Rate rate(1);//指定频率
    // while (true)
    // {
    //     ROS_INFO("-----------code----------");
    //     rate.sleep();//休眠，休眠时间 = 1 / 频率。
    // }

    //定时器--------------------------------------
    ros::Timer timer = nh.createTimer(ros::Duration(0.5),cb,true,false);//后面两个bool，前一个是是否只执行一次，后一个是是否自动启动，若填了false，就要后面timer.start();    ros::spin();//前面有回调函数cb，故要spin
    return 0;
}`
