  <template #header><strong>launch文件</strong></template>
  由于一个程序中可能要启动多个节点，比如小乌龟，要启动roscore、乌龟界面节点、键盘控制节点。如何优化？launch文件！<br/>
  实现：<br/>
  1、选定功能包（这里是hello_vscode下）右击新建文件夹launch<br/>
  2、创建start_turtle.launch<br/>
  3、写入内容:<br/>
  <highlightjs  language="c++" :autodetect="false" :code="code1"></highlightjs>

  4、在demo2_ws文件夹terminal下roslaunch hello_vscode start_turtle.launch<br/>
  5、添加刚刚写的hello_vscode_c：
  <highlightjs  language="c++" :autodetect="false" :code="code2"></highlightjs>
</el-card>
