</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-keyword">if</span> (<span class="cm-variable">getHash</span>(<span class="cm-variable">l1</span>, <span class="cm-variable">r1</span>) <span class="cm-operator">==</span> <span class="cm-variable">getHash</span>(<span class="cm-variable">l2</span>, <span class="cm-variable">r2</span>)) <span class="cm-variable">puts</span>(<span class="cm-string">"Yes"</span>);</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"><span cm-text="" cm-zwsp="">
