 1、用法：roscd roscpp ，会直接切换到/opt/ros/noetic/share/roscpp的cmd中<br/>
 再pwd，可以输出当前工作目录<br/>
 查看可以切换到那些：echo $ROS_PACKAGE_PATH<br/>
2、查看ROS日志文件的目录：roscd log<br/>
