<template>
    <canvas ref="liveCanvas" style="position:absolute;left:1500px;top:260px;"></canvas>
</template>

<script>
    import * as PIXI from 'pixi.js';
    import { beforeUnmount } from 'vue';
    import { Live2DModel } from 'pixi-live2d-display/cubism4';
    window.PIXI = PIXI; // 为了pixi-live2d-display内部调用
    let app; // 为了存储pixi实例
    let model; // 为了存储live2d实例
    export default {
        async mounted(){
            app = new PIXI.Application({
                view: this.$refs.liveCanvas,
                autoStart: true,
                resizeTo: window,
                backgroundAlpha: 0,
            });
            // static/live2dw/model/naxidaqwq.model3.json
            // static/live2dw/model/MissBai.model3.json
            // static/live2dw/model/Nahida_1080/Nahida_1080.model3.json
            //static/live2dw/runtime/hiyori_pro_t11.model3.json
            model = await Live2DModel.from('static/live2dw/model/Nahida_1080/Nahida_1080.model3.json');
            // draggable(model);
            app.stage.addChild(model);
            model.scale.set(0.05); // 调整缩放比例，一般原始资源尺寸非常大，需要缩小
            var hitAreas = model.internalModel.hitAreas;
            model.y=200
            model.on("pointerdown", (hitAreas)=>{
                // e 包含点击的区域和鼠标坐标
                const { x, y } = hitAreas.data.global 
                const point = model.hitTest(x, y)
                console.log('1')
                model.expression("kuku");
            });
           }
// ,

