    <script setup>
    var code1=`#include<stdio.h>
const int N =1E6+10;
int st[N];//代表N是否已被筛掉
int primes[N],cnt;//primes代表从第一个到最后一个的质数值
int main(){
    int n;
    scanf("%d",&n);
    for(int i = 2 ; i <= n ; i ++)
        if(!st[i])
        {
            primes[cnt++]=i;
            for(int j = i+i ; j <=n;j+=i)st[j]=1;
        }
    printf("%d",cnt);

}
`
    var code2=`int linear_sieve_prime(int n){
    // n只会被最小质因子筛去
    for(int i =2 ;i <=n;i++){
        if(!st[i])primes[cnt++] = i;
        for(int j = 0;primes[j]<=n/i;j++){
            st[primes[j]*i] = 1;
            if(i % primes[j] == 0)break;
        }
    }
    return cnt;
}`
    </script>
