cm-operator">&gt;&gt;</span><span class="cm-variable">a</span>[<span class="cm-variable">i</span>];</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-variable">deque</span><span class="cm-operator">&lt;</span><span class="cm-variable-3">int</span><span class="cm-operator">&gt;</span> <span class="cm-variable">q</span>;</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-keyword">for</span>(<span class="cm-variable-3">int</span> <span class="cm-variable">i</span> <span class="cm-operator">=</span> <span class="cm-number">0</span> ; <span class="cm-variable">i</span> <span class="cm-operator">&lt;</span><span class="cm-variable">n</span>;<span class="cm-variable">i</span><span class="cm-operator">++</span>){</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-comment">//1  3  -1  -3  5  3  6  7q的size为0，首先1不进入while，而进入q。两个if不执行（i&lt;m-1且i-m&lt;0）。然后3，由于q.back()（1）比3小，故不进入while，3也进入q，此时为1,3，两个if不执行。-1进入时，由于q.back()（3）比-1大，故删除3，又q.back()（1）比-1大，又删除1，然后执行第二句，-1进入q，第三句不执行，执行第四句，输出-1。此时q里为-1。然后q.back()（-1）比-3大，q：-1，删除-1，插入-3，-3=q.front()！=a[i-m]=1，第一个if不执行，输出-3，此时q里为-3。q.back()（-3）比5小，此时q中为-3,5，输出-3。q.back()（5）比3大，删除5，q内为-3,3，输出-3。q.back()（3）比6小，此时q内为-3,3,6,-3=q.front()==a[i-m]=-3，执行第一个if删除-3，q为3,6，输出3...</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-keyword">while</span>(<span class="cm-variable">q</span>.<span class="cm-variable">size</span>()<span class="cm-operator">&amp;&amp;</span><span class="cm-variable">q</span>.<span class="cm-variable">back</span>()<span class="cm-operator">&gt;</span><span class="cm-variable">a</span>[<span class="cm-variable">i</span>])<span class="cm-variable">q</span>.<span class="cm-variable">pop_back</span>();</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable">q</span>.<span class="cm-variable">push_back</span>(<span class="cm-variable">a</span>[<span class="cm-variable">i</span>]);</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-keyword">if</span>(<span class="cm-variable">i</span><span class="cm-operator">-</span><span class="cm-variable">m</span><span class="cm-operator">&gt;=</span><span class="cm-number">0</span><span class="cm-operator">&amp;&amp;</span><span class="cm-variable">q</span>.<span class="cm-variable">front</span>()<span class="cm-operator">==</span><span class="cm-variable">a</span>[<span class="cm-variable">i</span><span class="cm-operator">-</span><span class="cm-variable">m</span>])<span class="cm-variable">q</span>.<span class="cm-variable">pop_front</span>();<span class="cm-comment">//if只有i&gt;=m且队首指针马上要pass窗口的第一个位置时才执行，把窗口的第一个删掉</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-keyword">if</span>(<span class="cm-variable">i</span><span class="cm-operator">&gt;=</span><span class="cm-variable">m</span><span class="cm-operator">-</span><span class="cm-number">1</span>)<span class="cm-variable">cout</span><span class="cm-operator">&lt;&lt;</span><span class="cm-variable">q</span>.<span class="cm-variable">front</span>()<span class="cm-operator">&lt;&lt;</span><span class="cm-string">" "</span>;<span class="cm-comment">//只有第x（x&gt;=m时）个才会进行输出</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  }</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-variable">q</span>.<span class="cm-variable">clear</span>();</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-variable">cout</span><span class="cm-operator">&lt;&lt;</span><span class="cm-variable">endl</span>;</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> <span class="cm-keyword">for</span>(<span class="cm-variable-3">int</span> <span class="cm-variable">i</span> <span class="cm-operator">=</span> <span class="cm-number">0</span> ; <span class="cm-variable">i</span> <span class="cm-operator">&lt;</span><span class="cm-variable">n</span>;<span class="cm-variable">i</span><span class="cm-operator">++</span>){</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-keyword">while</span>(<span class="cm-variable">q</span>.<span class="cm-variable">size</span>()<span class="cm-operator">&amp;&amp;</span><span class="cm-variable">q</span>.<span class="cm-variable">back</span>()<span class="cm-operator">&lt;</span><span class="cm-variable">a</span>[<span class="cm-variable">i</span>])<span class="cm-variable">q</span>.<span class="cm-variable">pop_back</span>();<span class="cm-comment">//相比上一段，这段只改了这句的&gt;</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable">q</span>.<span class="cm-variable">push_back</span>(<span class="cm-variable">a</span>[<span class="cm-variable">i</span>]);</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-keyword">if</span>(<span clas