<template>


    <el-card style="max-width: 1200px">
        <template #header><strong>人的视觉原理</strong></template>
       <div class="card">人如何能把看到的图像转化成这个是猫、哪个是狗呢？如果把之前深度学习运用到计算机中，就会效率很低，但人的眼睛却很准确，为什么呢？首先要讲的是神经认知。
       实际上人眼看到的是这样的：<br/><div style="text-align: center;"> 像素点→ 边缘、方向 → 轮廓、细节 → 判断</div><br/></div>
    </el-card>
    <div style="height: 30px;"></div>

    <el-card style="max-width: 1200px">
        <template #header><strong>卷积神经网络</strong></template>
        <div class="card">是一种实用的图片识别方法，简写为CNN。什么是卷积？通信上常常会提到卷积，但他们不一样。这里的卷积指用数学的方法提取出图像特征。比方说有一张x的像素图，
       在计算机看来就是一大堆数字
       <el-row class="row-bg" justify="space-evenly">
        <el-col :span="9"><img src="../../../assets/pics/science/x.png" style="width: 70%;height: auto"></el-col>
        <el-col :span="9"><img src="../../../assets/pics/science/x_com.png" style="width: 70%;height: auto"></el-col>
    </el-row>
计算机如何看他是一个x还是什么呢，首先要提取特征，提取特征的方法就是使用卷积核来做卷积运算。卷积核也是一个3*3或5*5的方块。例如有如下一个卷积核：
<br/>
<el-row class="row-bg" justify="space-evenly">
        <el-col :span="18"><img src="../../../assets/pics/science/juanjihe1.png"></el-col>
    </el-row><br/>将它与左下图的每个3*3方块进行运算，形成右下图所示的特征图(3*3 乘以3*3，全部相加)<br/>
<el-row class="row-bg" justify="space-evenly">
        <el-col :span="9"><img src="../../../assets/pics/science/juanjiyunsuan.png" style="width: 70%;height: auto"></el-col>
        <el-col :span="9"><img src="../../../assets/pics/science/tezhengtu1.png"></el-col>
    </el-row>
    <br/>
    实际上这个卷积核就对应了一个特征，就是一个斜向右下的线条。而在特征图中数字比较大的部位就满足这个特征，数字越小就越不满足我想要一个圈，一个竖着的，也是同理。
    总而言之，通过不同的卷积核，我们就能够对图像进行不同的处理，得到不同的特征图，显示出来这种特征在图像的那一个位置。
    <br/>卷积之后下一个步骤就是池化和激活。
</div>
    </el-card>
    <div style="height: 30px;"></div>

    <el-card style="max-width: 1200px">
        <template #header><strong>池化、激活</strong></template>
       <div class="card">什么叫池化？比如对上面所说的特征图进行池化，采用最大池化的方法。把特征值2*2或2*1或1*2或1*1中最大的值取出，如左下图所示
        ，最后得到比较小的特征图，如右下所示，但这个特征图其实依然保留了原来特征图的很重要的信息<br/>
        <el-row class="row-bg" justify="space-evenly">
        <el-col :span="9"><img src="../../../assets/pics/science/chihua.png" style="width: 50%;height: auto"></el-col>
        <el-col :span="9"><img src="../../../assets/pics/science/chihuahoutezhengtu.png"></el-col>
    </el-row>
    <br/>
    池化后还需要激活，还记得之前机器学习中讲的激活函数吗？sigmoid函数。过程如下图所示：
    <el-row class="row-bg" justify="space-evenly">
        <el-col :span="9"><img src="../../../assets/pics/science/jihuo.png" style="width: 140%;height: auto"></el-col>
    </el-row>
    训练的过程就是不断地对图片进行卷积、池化、卷积、池化，经过多次卷积和池化之后，就可以找到很多特征，比如水平的线条、倾斜的线条、轮廓、颜色等。把足够多的
    信息再接入全连接网络进行训练，最后就可以判断这个图像是什么了。这就是一个卷积神经网络的全貌。
       </div>
    </el-card>
    <div style="height: 30px;"></div>
    
    <el-card style="max-width: 1200px">
        <template #header><strong>算法、数据、算力</strong></template>
       <div class="card">算法、数据、算力缺一不可，算法讲完了，接下来讲数据。
        数据其实就是用于人工智能训练的东西。例如你平时填验证码时一些事填字母和数字，一些是让你识别图片，这些可能就是利用你
        在训练。算力是指进行卷积运算的运算量很大，用一张800*600像素的图片举例，rgb红光绿，故有800*600*3=1440000个像素点，用3*3*3（rgb，故再乘一个
        3）的卷积核做卷积，大概需要1300万次乘法和1200万次加法。你要有成千上万张图片进行成千上万次处理，所以运算量非常大。用什么进行计算呢？用处理器（芯片
        ，即CPU），它什么都能算，但只能串行。后来人们发现了一种比CPU更适合的东西，叫GPU图形处理器，它最开始是用来计算机中图像投影到屏幕上。它的专用性
        强，GPU只会算加法和乘法，蛋挞并行能力强，可以同时算一大堆加法和乘法。现在有了更强的NPU神经网络处理器，是专门用来人工智能运算的处理器，并行能力更强了。

       </div>
    </el-card>
