<template>


    <el-card style="max-width: 1200px">
        <template #header><strong>二分查找</strong></template>
    例如1，2，2，3，3，4，比如查找3，前者（第一个算法）用于查找左侧的3，右侧为右侧。
    
        <highlightjs  language="c++" :autodetect="false" :code="code"></highlightjs>
    

    
      </el-card>
    
    </template>
    <script setup>
    var code=`//查找左边界 SearchLeft 简写SL
int SL(int l, int r)
{
    while (l < r)
    {
        int mid = l + r >> 1;
        if (check(mid)) r = mid; 
        else l = mid + 1; 
    }   
    return l;
}
//查找右边界 SearchRight 简写SR 
int SR(int l, int r) 
{
    while (l < r)
    {                   
        int mid = l + r + 1 >> 1; //需要+1 防止死循环
        if (check(mid)) l = mid;
        else r = mid - 1; 
    }
    return r; 
}`
    </script>
