    半监督式学习问题介于监督式和非监督式学习之间。这里有一个好例子如：照片分类，但是只有部分照片带有标签(如，狗、猫和人)，但是大部分照片都没有标签。<br/>
<strong>多视图聚类</strong>：caltech101-7数据集，有1474个样本，我们对这些样本做不同的处理，得到不同的特征。<br/>
48 维的 Gabor 特征、<br/>
40 维的 wavelet moments (WM)、<br/>
254 维的 CENTRIST 特征、<br/>
1984 维的 HOG 特征、<br/>
512 维的 GIST 特征、<br/>
928 维的 LBP 特征。<br/>
