<template>
  <el-card style="max-width: 1200px">
    <template #header><strong>示意图</strong></template>
    初始：head=-1,idx=0;  要插入（头插法）：1,3,2,5,4  最后希望结果：4→5→2→3→1&emsp;&emsp;<el-text tag="pub" size="large">//e[idx]=x;&emsp;ne[idx]=head&emsp;head=idx++;</el-text><br/>
