    <strong>add_head=3</strong>:&emsp;   &emsp;e[1]=3,&emsp;ne[1]=0,&emsp;head=1,&emsp;idx=2<br/>
    <strong>add_head=2</strong>:&emsp;   &emsp;e[2]=2,&emsp;ne[2]=1,&emsp;head=2,&emsp;idx=3<br/>
    <strong>add_head=5</strong>:&emsp;   &emsp;e[3]=5,&emsp;ne[3]=2,&emsp;head=3,&emsp;idx=4<br/>
    <strong>add_head=4</strong>:&emsp;   &emsp;e[4]=4,&emsp;ne[4]=3,&emsp;head=4,&emsp;idx=5<br/>
