    <strong>ne</strong>：-1 0 1 2 3<br/>
    <strong>head</strong>=4&emsp;&emsp;  idx=5<br/>
    e[head]=e[4]=4 &emsp;  ne[4]=3 &emsp;  e[3]=5.....符合<br/>
  </el-card>
