  <div class="demo-collapse" >
    <el-collapse v-model="activeNames" accordion>
      <el-collapse-item title="建表" name="1" class="manual">
        <div style="font-size: 15px">
            建表：create 'student','Sname','Ssex','Sage','Sdept','course'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val10)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/>
            看表：describe 'student'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val11)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/>
        </div>
      </el-collapse-item>
      <el-collapse-item title="插入" name="2" >
        <div style="font-size: 15px"> 
        插入数据：put 'student','95001','Sname','LiYing'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val12)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/>
注意：在这shell模式下一次只能加一个数据，上面这条的行键为95001<br/><br/>
为studect表的95001号在course列族的math列添加数据：put 'student','95001','course:math','80'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val13)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/>
<br/>为95001行下的Ssex列添加了一个数据：put 'student','95001','Ssex','Male'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val14)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/>
</div>
      </el-collapse-item>
      <el-collapse-item title="删除" name="3">
        <div style="font-size: 15px">
        删除student表中95001行下的Ssex列的所有数据：  delete 'student','95001','Ssex'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val15)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/><br/>
删除student表中的95001行的全部数据：deleteall 'student','95001'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val16)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/><br/>
删除表有两部，第一步先让该表不可用，第二步删除表：<br/>
disable 'student'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val17)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip>  <br/>
drop 'student'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val18)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/><br/>
</div>
      </el-collapse-item>
      <el-collapse-item title="查询" name="4" >
        
        <div style="font-size: 15px">
        查看‘student’表‘95001’行的数据：get 'student','95001'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val19)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/><br/>
返回‘student’表的全部数据：scan 'student'<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val20)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/><br/>
查询表的历史版本：<br/>
1、创建表的时候，指定保存的版本数（假设为5）：create 'teacher',{NAME=>'username',VERSIONS=>5}<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val21)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/>
2、插入数据然后更新数据，使其产生历史版本数据：<br/>
put 'teacher','91001','username','Mary'<br/>
put 'teacher','91001','username','Mary1'<br/>
put 'teacher','91001','username','Mary2'<br/>
put 'teacher','91001','username','Mary3'<br/>
put 'teacher','91001','username','Mary4'  <br/>
put 'teacher','91001','username','Mary5'<br/>
3、查询时，指定查询的历史版本数。（默认会查询出最新的数据）：（1-5）<br/>
get 'teacher','91001',{COLUMN=>'username',VERSIONS=>3}<el-tooltip
        class="box-item"
        effect="dark"
        content="复制"
        placement="right"
      >
      <el-button @click="copyClick(val22)" link><el-icon><CopyDocument /></el-icon></el-button></el-tooltip><br/><br/>
</div>
