    <script setup>
    import useClipboard from 'vue-clipboard3';
    import { ref } from 'vue'
    import { ElNotification } from "element-plus";
    import {
    CopyDocument
  } from '@element-plus/icons-vue'
    var val1='cd /usr/local/hadoop'
    var val0='ssh localhost'
    var val2='./sbin/start-dfs.sh'
    var val3='cd /usr/local/hbase'
    var val4='bin/start-hbase.sh'
    var val5='bin/hbase shell'
    var val6='exit'
    var val7='bin/stop-hbase.sh'
    var val8='cd /usr/local/hadoop'
    var val9='./sbin/stop-dfs.sh'
    var val10=`create 'student','Sname','Ssex','Sage','Sdept','course'`
    var val11=`describe 'student'`
    var val12=`put 'student','95001','Sname','LiYing'`
    var val13=`put 'student','95001','course:math','80'`
    var val14=`put 'student','95001','Ssex','Male'`
    var val15=`delete 'student','95001','Ssex'`
    var val16=`deleteall 'student','95001'`
    var val17=`disable 'student'`
    var val18=`drop 'student'`
    var val19=`get 'student','95001'`
    var val20=`scan 'student'`
    var val21=`create 'teacher',{NAME=>'username',VERSIONS=>5}`
    var val22=`get 'teacher','91001',{COLUMN=>'username',VERSIONS=>3}`
    const succeed = () => {
  ElNotification({
    title: "复制成功",
    message: "✈ ✈ ✈ ✈ ✈ ✈",
    type: "success",
    position: 'top-left'
  });
};
const activeNames = ref(['1'])
const { toClipboard } = useClipboard();
    /*
    * 点击的方法
    * @item 需要复制的内容
    */
    const copyClick = async item => {
      try {
        await toClipboard(item);
        succeed()
      } catch (e) {
        console.error(e);
      }
    };

    </script>
